export const fetchData = async () => {
  try {
    const response = await fetch("https://crypto.mobilelab.vn/v1/cryptotrading/setting/coins" );
//     const response = await Promise.all([
      
// fetch('https://crypto.mobilelab.vn/v1/cryptotrading/setting/coins' ).then((response)=> response.json()),
// fetch('https://crypto.mobilelab.vn/v1/cryptotrading/setting/markets' ).then((response)=> response.json())
    
//     ])
    const data = await response.json();
    return data;
  } catch (e) {
    console.log(e);
  }
};

